<template>
    <div>
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>markdown编辑器</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container">
          <el-row :gutter="20">
            <el-col :span="12">
              <el-form :model="mdinfoList">
                <el-form-item label="标题" prop="title">
                  <el-input v-model="mdinfoList.title" style="width: 200px"></el-input>
                </el-form-item>
                <el-form-item label="分类" prop="type">
                  <el-autocomplete
                      class="inline-input"
                      v-model="mdinfoList.type"
                      :fetch-suggestions="querySearch"
                      placeholder="请输入分类"
                  ></el-autocomplete>
                </el-form-item>
                <el-form-item label="简介" prop="summary">
                  <el-input v-model="mdinfoList.summary" style="width: 400px" type="textarea" :autosize="{ minRows: 2, maxRows: 5}"></el-input>
                </el-form-item>
              </el-form>
            </el-col>
            <el-col :span="6">
              <el-upload drag
                         accept="image/gif,image/jpeg,image/jpg,image/png,image/svg"
                         :action="putImgurl" :on-success="handleAvatarSuccess"
                         multiple>
                <img id="img" v-if="img" :src="img" class="avator" alt="">
                <i class="el-icon-upload"></i>
                <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
                <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div>
              </el-upload>
            </el-col>
          </el-row>
            <mavon-editor v-model="content" ref="md" @imgAdd="$imgAdd" @change="change" style="min-height: 600px"/>
            <el-button class="editor-btn" type="primary" @click="submitmsg">提交</el-button>
        </div>
    </div>
</template>

<script>
    import axios from "axios";
    import { mavonEditor } from 'mavon-editor'
    import 'mavon-editor/dist/css/index.css'
    export default {
        name: 'markdown',
        data: function(){
            return {
              img:'',
              putImgurl: 'http://localhost/oss/putImg',
                content:'',
                html:'',
                configs: {
                },
                mdinfoList: {
                  title: '',
                  type: '',
                  summary: '',
                  subimg: '',
                },
              typeList:[
                {"value":"生活"},
                {"value":"游戏"}
              ],
            }
        },
        components: {
            mavonEditor
        },
        methods: {
          handleAvatarSuccess(response,file) {
            this.img = URL.createObjectURL(file.raw);
            this.mdinfoList.subimg = response;
            console.log(this.mdinfoList.subimg);
          },
            $imgAdd(pos, $file){
                var formdata = new FormData();
                formdata.append('file', $file);
                this.axios({
                    url: 'http://127.0.0.1:2333/putImg',
                    method: 'post',
                    data: formdata,
                    headers: { 'Content-Type': 'multipart/form-data' },
                }).then((url) => {
                    this.$refs.md.$img2Url(pos, url);
                })
            },
            change(value, render){
                // render 为 markdown 解析后的结果
                this.html = render;
            },
            submitmsg(){
            let file = '';
            axios({
              url:'/oss/putArc',
              method:'post',
              data:{
                "content":this.content
              }
            }).then(res =>{
              file = String(res.data);
              console.log(file);
              axios({
                url:'/admin/putArc',
                method: 'POST',
                data: {
                  "title": this.mdinfoList.title,
                  "type": this.mdinfoList.type,
                  "summary": this.mdinfoList.summary,
                  "subimg": this.mdinfoList.subimg,
                  "content": file
                }
              }).then(res =>{
                if(res.data.code==200){
                  this.$message.success('提交成功！');
                  this.router.push('/articles');
                }else {
                  this.$message.error('提交失败！');
                }
              })
            });
            },
          querySearch(queryString, cb) {
            var types = this.typeList;
            var results = queryString ? types.filter(this.createFilter(queryString)) : types;
            // 调用 callback 返回建议列表的数据
            cb(results);
          },
          createFilter(queryString) {
            return (type) => {
              return (type.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
            };
          },
          getTypeList(){
            axios({
              url:"/admin/getTypeList",
              method:'get'
            }).then(res =>{
              this.typeList = Array(res.data.dataList)[0];
            })
          }
          },
      mounted() {
          this.getTypeList();
      }
    }
</script>
<style scoped>
    .editor-btn{
        margin-top: 20px;
    }
</style>
